import React, { useState, useEffect } from 'react'
import { Layout, Icon } from 'antd'
import { withRouter, RouteComponentProps } from 'react-router-dom'
import BasicLayout from './BasicLayout'
import menuData from '@/config/menu'
import styles from './index.scss'

const { Header, Sider, Content } = Layout;

interface IProps extends RouteComponentProps {
  children: React.ReactNode
  whiteRoute: string[]
}

const Index = (props: IProps) => {
  const { children, whiteRoute, history } = props
  const [collapsed, setCollapsed] = useState(false)
  const [collapseClickStatus, setCollapseClickStatus] = useState(false) // 
  const [pathname, setPathname] = useState('')

  //  window.location.pathname state
  // content
  //  re-render   whiteRoute 
  useEffect(() => {
    setPathname(window.location.pathname)
  }, [whiteRoute, children, window.location.pathname])

  const onCollapse = (collapsed, type) => {
    setCollapsed(collapsed ? true : false)
    setCollapseClickStatus(collapseClickStatus ? false : true)
  }

  const trigger = (
    <Icon type={collapsed ? 'menu-unfold' : 'menu-fold'} onClick={() => onCollapse} />
  )

  if (whiteRoute?.length && whiteRoute.includes(pathname)) {
    return (
      <Layout className={styles.layout}>
        <Header style={{ background: 'none' }}>Header</Header>
        <Content>
          {children}
        </Content>
      </Layout>
    )
  }

  return (
    <Layout className={styles.layout}>
      <Header style={{ background: 'none' }}>Header</Header>
      <Layout className={collapseClickStatus ? styles.collapsedMenu : ''}>
        <Sider
          theme="light"
          collapsible
          collapsed={collapsed}
          onCollapse={onCollapse}
          trigger={trigger}
          collapsedWidth={56}
        >
          <div
            onMouseEnter={(e) => {
              collapseClickStatus && collapsed ? setCollapsed(false) : null
            }}
            onMouseLeave={(e) => {
              collapseClickStatus && !collapsed ? setCollapsed(true) : false
            }}
          >
            <BasicLayout menuData={menuData} collapsed={collapsed} />
          </div>
        </Sider>
        <Content>
          {children}
        </Content>
      </Layout>
    </Layout>
  )
}

export default withRouter<IProps, any>(Index);