interface IState {
  count: number
}

interface IReducers {
  add: () => any
  reduce: () => any
}

const model: Model<IState, IReducers> = {
  namespace: 'test',
  state: {
    count: 0,
  },
  reducers: {
    // 
    async add() {
      const { count } = this.getModelState()
      console.log("", this.getState());
      this.setState({
        count: count + 1,
      })
    },

    // 
    async reduce() {
      const { count } = this.getModelState()
      console.log("", this.getState());
      this.setState({
        count: count - 1,
      })
    }
  }
}

export default model